import ScrollDownIndicator from '../components/ScrollDownIndicator';
import Link from 'next/link';
// Importando ícones da biblioteca Phosphor Icons
import { PiCompass, PiClock, PiCode } from 'react-icons/pi';
import { RiDoubleQuotesR } from 'react-icons/ri';

export default function HomePage() {
    return (
        <>
            {/*Seção que terá a imagem de fundo */}
            <section className="hero-section">
                <div className="hero-content">
                    <h1>Suas ideias, <strong className='destaque_title'>amadurecidas</strong> em software.</h1>
                    <p>Transformando ideias em soluções digitais robustas e personalizadas.</p>
                    <div className="hero-buttons">
                        <Link href="/solicitacao" className="hero-cta-primary">Solicitar Projeto</Link>
                        <Link href="/portfolio" className="hero-cta-secondary">Conheça nosso Portfólio</Link>
                    </div>
                </div>
                <ScrollDownIndicator />
            </section>

            {/* Seção de destino para a rolagem */}
            <section className="next-section">
                <div><h2 className='title_section_about'>A precisão do <strong className='title_header'>Software sob demanda</strong></h2>
                <p className='subtitle_section_about'>
                    Nosso processo é focado em transparência, excelência técnica e resultados reais.
                </p>
                </div>
                <div className="benefits_container">
                    <div className="benefit-item">
                        <PiCompass className="benefit-icon" />
                        <h3 className="title_benefits">Maturidade técnica</h3>
                        <p className="text_benefits">Antes da primeira linha de código, mergulhamos no seu negócio. Entendemos seu desafio para desenhar a arquitetura exata que sua operação precisa.</p>
                    </div>
                    <div className="benefit-item">
                        <PiClock className="benefit-icon" />
                        <h3 className="title_benefits">Entrega pontual</h3>
                        <p className="text_benefits">Prazos definidos e cumpridos. Como os ciclos do outono, nossas entregas são previsíveis e transparentes.</p>
                    </div>
                    <div className="benefit-item">
                        <PiCode className="benefit-icon" />
                        <h3 className="title_benefits">Código limpo</h3>
                        <p className="text_benefits">Cada linha de código é escrita pensando em manutenibilidade e performance a longo prazo.</p>
                    </div>
                </div>
            </section>
            <section className='compromise_container'>
                <div>
                    <h2 className='title_section_compromise'>Como <strong className='title_compromise'>cultivamos</strong> seu software</h2>
                </div>
                <div className='compromise_banner'>
                    <div className='compromise_card'>
                        <RiDoubleQuotesR className="compromise-quote-icon" />
                        <h3 className='compromise_item'>"Iniciamos com uma imersão profunda no seu desafio. Não pulamos para o código antes de mapear a estratégia e a arquitetura ideal para o seu sucesso."</h3>
                        <p className='compromise_phase'>Fase 1: Estratégia e Descoberta</p>
                        <p className='compromise_description'>O alicerce de todo projeto.</p>
                    </div>
                    <div className='compromise_card'>
                        <RiDoubleQuotesR className="compromise-quote-icon" />
                        <h3 className='compromise_item'>"Desenvolvimento ágil com ciclos curtos e entregas previsíveis. Você vê o progresso, valida as etapas e tem total visibilidade do produto do início ao fim."</h3>
                        <p className='compromise_phase'>Fase 2: Desenvolvimento e Colaboração</p>
                        <p className='compromise_description'>Transparência e qualidade em primeiro lugar.</p>
                    </div>
                </div>
            </section>
        </>
    );
}